import React, { useState, useEffect } from 'react';
import { Form, Input, Button, Radio, Tabs } from 'antd';
import { saveDraft, submitCheck } from '../../../api/reportApi';
import './MemberCheck.css';

// const { TabPane } = Tabs;

const testStudents = [
  { id: '1', name: '' },
  { id: '2', name: '' },
  { id: '3', name: '' },
  { id: '4', name: '' },
  { id: '5', name: '' },
  { id: '6', name: '' },
  { id: '7', name: '' },
  { id: '8', name: '' },
  { id: '9', name: '' },
];

const MemberCheck = () => {
  const [selectedStudent, setSelectedStudent] = useState(testStudents[0].id);
  const [studentData, setStudentData] = useState({});

  const handleSaveDraft = async (values) => {
    try {
      const response = await saveDraft({ ...values, status: 0, studentId: selectedStudent });
      console.log('  :', response);
    } catch (error) {
      console.error('  :', error);
    }
  };

  const handleSubmit = async (values) => {
    try {
      const response = await submitCheck({ ...values, status: 1, studentId: selectedStudent });
      console.log(' :', response);
    } catch (error) {
      console.error(' :', error);
    }
  };

  const handleValuesChange = (changedValues, allValues) => {
    setStudentData({
      ...studentData,
      [selectedStudent]: allValues,
    });
  };

  const items = testStudents.map(student => ({
    key: student.id,
    label: student.name,
    children: (
      <StudentForm
        key={student.id}
        studentId={student.id}
        initialValues={studentData[student.id] || {}}
        handleSaveDraft={handleSaveDraft}
        handleSubmit={handleSubmit}
        handleValuesChange={handleValuesChange}
      />
    ),
  }));

  return (
    <div className="member-check-section">
      <h2 className="section-title"> </h2>
      <Tabs activeKey={selectedStudent} onChange={setSelectedStudent} items={items} />
    </div>
  );
};

const StudentForm = ({ studentId, initialValues, handleSaveDraft, handleSubmit, handleValuesChange }) => {
  const [form] = Form.useForm();

  useEffect(() => {
    form.setFieldsValue(initialValues);
  }, [initialValues, form]);

  return (
    <Form
      form={form}
      onFinish={handleSubmit}
      layout="vertical"
      onValuesChange={handleValuesChange}
      initialValues={initialValues}
    >
      <Form.Item label={<strong> ?( )</strong>} name="attendance"> 
        <Radio.Group>
          <Radio value="1"></Radio>
          <Radio value="0"></Radio>
        </Radio.Group>
      </Form.Item>
      <Form.Item label={<strong>GQS ?</strong>}  name="gqs" >
        <Radio.Group>
          <Radio value=""></Radio>
          <Radio value=" "></Radio>
        </Radio.Group>
      </Form.Item>
      <Form.Item label={<strong> </strong>} name="training_content">
        <Input.TextArea 
        className="form-item-input" 
        rows={3}
        placeholder='    .'
        />
      </Form.Item>
      <Form.Item label={<strong> </strong>} name="pray_member">
        <Input.TextArea 
        className="form-item-input" 
        rows={3}
        placeholder='   .'
        />
      </Form.Item>
      <Form.Item className="button-group">
        <Button onClick={() => handleSaveDraft(form.getFieldsValue())}> </Button>
        <Button type="primary" htmlType="submit"></Button>
      </Form.Item>
    </Form>
  );
};

export default MemberCheck;


