import React from 'react';
import { Form, Input, Button } from 'antd';
import './Issue.css';
import { saveDraft, submitCheck } from '../../../api/reportApi';

const Issue = () => {
    const [form] = Form.useForm();
  
    const handleSaveDraft = async (values) => {
      try {
        const response = await saveDraft({ ...values, status: 0 });
        console.log('  :', response);
      } catch (error) {
        console.error('  :', error);
      }
    };
  
    const handleSubmit = async (values) => {
      try {
        const response = await submitCheck({ ...values, status: 1 });
        console.log(' :', response);
      } catch (error) {
        console.error(' :', error);
      }
    };

    return(
        <div className="issue-section">
            <h2 className="section-title">   ?</h2>
            <Form
             form={form}
             onFinish={handleSubmit}
             layout='vertical'
            >
                <Form.Item className="form-item" label=" " name="issue_user">
                    <Input.TextArea
                    className="text-input form-item-input"
                    placeholder=",   , (, ,  ) ."
                    rows={4} //    
                    />
                </Form.Item>
                <Form.Item className="form-item" label=" " name="issue_Dept">
                    <Input.TextArea
                    className="text-input form-item-input"
                    placeholder=" ,      ."
                    rows={4} //    
                    />
                </Form.Item>
                <Form.Item className="button-group">
                    <Button className="draft-button" onClick={() => form.validateFields().then(handleSaveDraft)}> </Button>
                    <Button type="primary" htmlType="submit"></Button>
                </Form.Item>
            </Form>
        </div>
    );
};
export default Issue;