import React, { useState } from 'react';
import { Form, Input, Button } from 'antd';
import Logo from '../../Logo/Logo';
import { useNavigate } from 'react-router-dom';
import './Login.css';


const Login = ({ handleLoginSubmit }) => {
    const [message, setMessage] = useState('');
    const [id, setId] = useState('');
    const [password, setPassword] = useState('');
    const navigate = useNavigate(); //    navigate 
    
    const handleSubmit = async () => {
        try {
            await handleLoginSubmit(id, password);
            navigate('/dashboard'); //     
        } catch (error) {
            setMessage(error.message);
        }
    };

    const handleSendEmail = () => {
        navigate("/");
    }; 

    return (
        <div className="login-container">
            <div className="login-logo">
                <Logo />
            </div>
            <Form layout='vertical' className="login-form">
                <Form.Item>
                    <Input
            placeholder="()"
            value={id}
            onChange={(e) => setId(e.target.value)}
                    />
                </Form.Item>
                <Form.Item>
                <Input.Password
            placeholder=''
            value={password}
            onChange={(e) => setPassword(e.target.value)}
                    />
                </Form.Item>
            <p style={{ fontSize: '10px' }}>{message}</p>
            <Form.Item>
                <Button type="primary" htmlType="button" block onClick={handleSubmit}> 
                 
                </Button>
            </Form.Item>
            <Form.Item>
                <Button type="default" block onClick={handleSendEmail}>
                
                </Button>
            </Form.Item>
            </Form>
        </div>
    )
}

export default Login;