import React from 'react';
import { Form, Select, Input, Radio, Button } from 'antd';
import './UserCheck.css';
import { saveDraft, submitCheck } from '../../../api/reportApi';

const { Option } = Select;

const UserCheck = () => {
  const [form] = Form.useForm();

  const handleSaveDraft = async (values) => {
    try {
      const response = await saveDraft({ ...values, status: 0 });
      console.log('  :', response);
    } catch (error) {
      console.error('  :', error);
    }
  };

  const handleSubmit = async (values) => {
    try {
      const response = await submitCheck({ ...values, status: 1 });
      console.log(' :', response);
    } catch (error) {
      console.error(' :', error);
    }
  };

  return (
    <div className="user-check-section">
      <h2 className="section-title"> </h2>
      <Form 
        form={form} 
        onFinish={handleSubmit} 
        initialValues={{
          pray: '1',
          qt: '1',
          worship: '1 ',
          meeting: '',
        }} 
        layout="vertical"
      >
        <div className="horizontal-group">
          <Form.Item className="form-item-horizontal" label={<strong>   ?</strong>} name="pray">
            <Select 
              className="select-input" 
              getPopupContainer={trigger => trigger.parentNode}
              dropdownStyle={{ width: '200px', maxHeight: '256px', overflowY: 'auto' }}
            >
              {['0', '1', '2', '3', '4', '5', '6', '7'].map(value => (
                <Option key={value} value={value}>{value}</Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item className="form-item-horizontal" label={<strong>   QT ?</strong>} name="qt">
            <Select 
              className="select-input" 
              getPopupContainer={trigger => trigger.parentNode}
              dropdownStyle={{ width: '200px', maxHeight: '256px', overflowY: 'auto' }}
            >
              {['0', '1', '2', '3', '4', '5', '6', '7'].map(value => (
                <Option key={value} value={value}>{value}</Option>
              ))}
            </Select>
          </Form.Item>
        </div>

        <div className="horizontal-group">
          <Form.Item className="form-item-horizontal" label={<strong> ?</strong>} name="worship">
            <Radio.Group>
              <Radio value="1 ">1 </Radio>
              <Radio value="2 ">2 </Radio>
              <Radio value="3 ">3 </Radio>
              <Radio value=""></Radio>
            </Radio.Group>
          </Form.Item>
          <Form.Item className="form-item-horizontal" label={<strong> ?</strong>} name="meeting">
            <Radio.Group>
              <Radio value=""></Radio>
              <Radio value=""></Radio>
            </Radio.Group>
          </Form.Item>
        </div>

        <h3 className="subsection-title">   </h3>
        <Form.Item label=" " name="pray_youth">
          <Input.TextArea 
          className="text-input form-item-input" 
          placeholder=' ,    .'
          rows={4}//    
          />
        </Form.Item>
        <Form.Item label="GQS/" name="pray_group">
          <Input.TextArea 
          className="text-input form-item-input"
          placeholder='    .'
          rows={4}
           />
        </Form.Item>
        <Form.Item label=" " name="pray_user">
          <Input.TextArea 
          className="text-input form-item-input"
          placeholder='   .'
          rows={4}
          />
        </Form.Item>
        <Form.Item className="button-group">
          <Button className="draft-button" onClick={() => form.validateFields().then(handleSaveDraft)}> </Button>
          <Button type="primary" htmlType="submit"></Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default UserCheck;